/**
 * API
 */
export default class WxGameApi {
    static get userPath() {
        if (!WxGameApi.isRunInWeiXin) return
        return wx.env.USER_DATA_PATH + "/";;
    }
    static get isRunInWeiXin() {
        if (cc.sys.platform === cc.sys.WECHAT_GAME) {
            return true
        } else {
            WxGameApi.errorLog();
            return false;
        }
    }
    static onHide(callback) {
        if (!WxGameApi.isRunInWeiXin) return
        wx.onHide(callback);
    }
    static onShow(callback) {
        if (!WxGameApi.isRunInWeiXin) return
        wx.onShow(callback);
    }
    static errorLog(func) {
        //console.warn("[]");
    }

    static playBGM(bgmUrl) {
        if (!WxGameApi.isRunInWeiXin) return;
        let bgm = wx.createInnerAudioContext()
        // src  http(s) 
        bgm.src = bgmUrl;
        bgm.loop = true;
        bgm.autoplay = true;
        bgm.play()
        wx.onShow(() => {
            bgm.play();
        });
    }
    static playVideo(x, y, w, h, url ,overtime ,isAutoPlay, onComplete) {
        if (!WxGameApi.isRunInWeiXin) return null;
        let isPlayFinish = false;
        console.info("[]" + "<x,y>" + x + "," + y + "<w,h>" + w + "," + h);
        let video = wx.createVideo({
            x: x,
            y: y,
            width: w,
            height: h,
            src: url,
            poster: "",
            objectFit: "fill",
            controls: false,
            autoplay: isAutoPlay,
        });
        video.onEnded(() => {
            isPlayFinish = true;
            onComplete();
            video.destroy();
        });
        video.onerror(() => {
            isPlayFinish = true;
            onComplete();
            video.destroy();
        });
        //
        setTimeout(()=>{
            //
            if(!isPlayFinish){
                console.info("[]"+video);
                debugger;
                //
                if(video)video.destroy();
                onComplete();
            }
        },overtime);
        return video;
    }
    static downFile(url, path, successCallback, failCallback) {
        let userPath = WxGameApi.userPath;
        //console.info("[]" + userPath);
        wx.downloadFile({
            url: url,
            header: "",
            filePath: "",
            success(res) {
                console.info("[]" + res.statusCode);
                //console.info("[]" + res.tempFilePath);
                wx.getFileSystemManager().saveFile({
                    tempFilePath: res.tempFilePath,
                    filePath: userPath + path,
                    success(res) {
                        console.info("[]" + res.savedFilePath);
                        successCallback(res.savedFilePath);
                    },
                    fail(err) {
                        console.info("[]" + err.errMsg);
                    }
                });

            },
            fail(err) {
                console.info("[]" + err.errMsg);
                failCallback(err)
            }
        });
    }
    /**
     * 
     * @param {*} path 
     * @param {*} result 
     */
    static checkCacheExistFile(path, result) {
        wx.getFileSystemManager().access({
            path: path,
            success() {
                result(true);
            },
            fail() {
                result(false);
            }
        });
    }
    static readFile(path, successCallback, failCallback) {
        wx.getFileSystemManager().readFile({
            filePath: path,
            success(res) {
                console.info("[]" + res);
                successCallback(res);
            },
            fail(err) {
                console.info("[]" + err.errMsg);
                failCallback(err);
            }
        })
    }
    /**
     * 
     * @param {*} url 
     * @param {*} path 
     * @param {*} complete 
     */
    static getOrCacheResFile(url, path, complete) {
        //
        let isAllowDownload;
        if (!url || url === "") {
            isAllowDownload = false;
        } else {
            isAllowDownload = true;
        }
        let resPath = WxGameApi.userPath + path;
        //
        WxGameApi.checkCacheExistFile(resPath, (result) => {
            if (result === true) {
                console.info("[]]" + path);
                //
                complete(resPath);
            } else {
                //
                if (isAllowDownload) {
                    console.info("[]]" + path);
                    WxGameApi.downFile(url, path, (savedFilePath) => {
                        console.info("[]" + savedFilePath);
                        complete(savedFilePath);
                    }, (err) => {
                        console.info("[]" + err);
                        complete(null);
                    });
                } else {
                    console.info("[]]" + path);
                    //
                    complete(null);
                }
            }
        });
    }
    //
    static saveScore(value) {
        if (!WxGameApi.isRunInWeiXin) return
        console.info("[]" + value);
        //
        wx.getOpenDataContext().postMessage({
            message: {
                type: "command",
                function: "save",
                arguments: "score",
                data: value,
            }
        });
    }
}