import { inject } from "../../lib/framework/Injector/InjectDecorator";
import { MainModel } from "../Model/MainModel";
import { __IC_Model, ModelType } from "../util/Model";
import { IocView } from "../../lib/extensions/IocView";

const {ccclass,property} = cc._decorator

@ccclass
export default class Animal extends IocView {
    private value;
    private time;
    private step;
    onLoad() {
        super.onLoad();
    }
    start(){
        super.start();
    }
    /**
     * 
     * @param start  
     * @param duration 
     */
    config(start,duration){
        this.time = duration;
        this.value = start;

        this.step = this.time / this.value;
    }
    //
    play(){

    }
    
    update(dt){
        if(this.time>=0){
            let before = this.time;
            this.time -= dt;
            let after = Math.fround(this.time);
            //
            if(before !== after){
                if(after>=0){
                    this.getComponent(cc.Label).string = ""+after;
                }else{
                    this.getComponent(cc.Label).string = "Go~!";
                }
            }

        }else{
            this.destroy();
        }
    }
}