import {Singleton} from "../util/Singleton";
import {IModel} from "../util/Model";
import PrefabPool from "../helper/PrefabPool";

/**
 * 
 */
export class MainModel extends Singleton implements IModel {
    //
    public foodPool: PrefabPool = null;
    //
    public currentFoodNodes = [];
    //
    public lastFoodIndex = 0;
    //
    public lastTrack = 0;

    public animalDatas = [{
        image: 'animals/bird',
        type: '0',
        spriteFrame: null,
    }, {
        image: 'animals/bear',
        type: '1',
        spriteFrame: null
    }, {
        image: 'animals/rabbit',
        type: '2',
        spriteFrame: null,
    }, {
        image: 'animals/dog',
        type: '3',
        spriteFrame: null
    }]
    public foodDatas = [{
        image: 'foods/food-bird',
        type: '0',
        spriteFrame: null
    }, {
        image: 'foods/food-bear',
        type: '1',
        spriteFrame: null
    }, {
        image: 'foods/food-rabbit',
        type: '2',
        spriteFrame: null
    }, {
        image: 'foods/food-dog',
        type: '3',
        spriteFrame: null
    }]
    public rotateDur = 0.1
    public isRotationing = false
    public animalTextureRect = {
        width: 264,
        height: 268
    }

    public pauseFlag = true;
    //
    public isHide = false;
    //
    public isLookVideo = false;

    public score = 0;
    public timer = 0;
    public lastTimer = 0;
    public timePeriod = 2.5;
    public currentLevel = 0;


    public initTimePeriod = 0;

    public minTimePeriod = 0.5;

    public lastScore = 0;
    public maxScore = 0;

    public harderRatios = (() => {
        const list = []
        for (let i = 0; i <= 20; i++) {
            list[i] = [i * 2, 0.1]
        }
        return list
    })();
}